@import "mixin";
@import "header";



body {
	width: 100%;
	// min-width: 425px
	min-width: 1120px;
	margin: 0 auto; }

.bunner {
	// background: grey
	// height: 768px
	// padding-bottom: 100px
	position: relative;
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	flex-direction: column;
	background: url('../image/Picture.png')   no-repeat;
	background-size: cover;
	margin-bottom: 130px;
	.title {
		max-width: 910px;
		text-align: center;
		margin: 240px auto 0;
		font-family: "FuturaPTL";
		font-size: 44px;
		color: $white;
		text-transform: uppercase;
		line-height: 40px;
		position: relative;
		top: 25px;
		div {
			text-transform: none;
			font-size: 24px;
			font-family: "FuturaPTB"; } }
	.scroll {
		// text-align: center
		position: absolute;
		bottom: -28px;
		left: 50%;
		transform: translateX(-50%); } }



.grad {
	height: 224px;
	opacity: .7;
	background: rgba(0,0,0,0);
	background: -moz-linear-gradient(top, rgba(0,0,0,0) 0%, rgba(28,28,28,0) 0%, rgba(0,0,0,1) 100%);
	background: -webkit-gradient(left top, left bottom, color-stop(0%, rgba(0,0,0,0)), color-stop(0%, rgba(28,28,28,0)), color-stop(100%, rgba(0,0,0,1)));
	background: -webkit-linear-gradient(top, rgba(0,0,0,0) 0%, rgba(28,28,28,0) 0%, rgba(0,0,0,1) 100%);
	background: -o-linear-gradient(top, rgba(0,0,0,0) 0%, rgba(28,28,28,0) 0%, rgba(0,0,0,1) 100%);
	background: -ms-linear-gradient(top, rgba(0,0,0,0) 0%, rgba(28,28,28,0) 0%, rgba(0,0,0,1) 100%);
	background: linear-gradient(to bottom, rgba(0,0,0,0) 0%, rgba(28,28,28,0) 0%, rgba(0,0,0,1) 100%);
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#000000', endColorstr='#000000', GradientType=0 );
	vertical-align: middle;
	margin-top: 170px;
	.new {
		width: $width;
		max-width: 95%;
		margin: 50px auto 0;
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;
		justify-content: space-around;
		// position: relative
		// top: 55%
		// transform: translateY(-50%)
		.new-block {
			font-family: "FuturaPT";
			font-size: 24px;
			color: $white;
			display: inline-block;
			// vertical-align: top
			div {
				width: 85px;
				display: inline-block;
				vertical-align: top;

				div {
					margin-top: 10px;
					line-height: 20px;
					font-size: 16px;
					font-family: "FuturaPTB"; } }

			img {
				// vertical-align: top
				margin-right: 25px; } } } }


.why {
	width: $width;
	// max-width: 95%
	margin: 0 auto;
	text-align: center;
	.why-box {
		// height: 200px
		display: inline-block;
		font-size: 24px;
		font-family: "FuturaPT";
		margin: 0 auto 100px;
		text-align: center;
		vertical-align: top;
		color: $text;
		div {
			font-size: 24px;
			div {
				font-size: 16px;
				width: 215px;
				margin: 0	20px;
				color: lighten($text, 20%); } }
		img {
			margin-bottom: 30px; } } }
.site-h {
	text-transform: uppercase;
	font-size: 40px;
	font-family: "FuturaPTB";
	margin-top: 110px;
	margin-bottom: 70px;
	color: $text;
	text-align: center;
	span {
		color: $green; } }

.portfolio {
	width: 100%;
	// margin-bottom: 100px
	border-top: 1px solid grey;
	border-bottom: 1px solid grey;
	nav {
		text-align: center;
		margin: auto;
		margin-bottom: 110px;
		li {
			top: 0;
			transform: translateY(0%);
			a {
				color: $text;
				font-family: "FuturaPTH"; }
			a:hover {
				color: $green; } } }
	.slider {
		// height: 360px
		// background: grey
		margin-bottom: 65px; } }
.sl-capt {
	padding: 275px 35px 0 30px;
	font-family: "FuturaPT";
	font-size: 24px;
	color: $white;
	text-transform: uppercase; }
.sl2 {
	padding-top: 10px;
	padding-bottom: 20px;
	font-size: 18px;
	line-height: 16px;
	text-transform: none; }

#slider {
	position: relative; }

#slides {}

.inner {
	width: 250%; }
.inner:after {
	content: "";
	display: block;
	height: 0;
	clear: both; }

.slide-image:nth-child(-2n+5) {
	background: url("../image/Picture-4.png") center 0 no-repeat; }
.slide-image:nth-child(2) {
	background: url("../image/Picture-5.png") center 0 no-repeat; }
.slide-image:nth-child(3) {
	background: url("../image/Picture-6.png") center 0 no-repeat; }
.slide-image:nth-child(4) {
	background: url("../image/Picture-7.png") center 0 no-repeat; }
.slide-image:nth-child(6) {
	background: url("../image/Picture-5.png") center 0 no-repeat; }
.slide-image:nth-child(7) {
	background: url("../image/Picture-6.png") center 0 no-repeat; }
.slide-image:nth-child(8) {
	background: url("../image/Picture-7.png")	center 0 no-repeat; }

.slide-image {
	float: left;
	width: 10%;
	height: 100%;
	box-sizing: border-box;
	border: 2px solid $white;
	display: block; }



#overflow {
	width: 100%;
	height: 355px;
	margin: auto auto;
	overflow: hidden; }

#slider input {
	display: none; }

#controls {
	position: absolute;
	width: 100%;
	top: 50%;
	transform: translateY(-50%);
	height: 50px;
	z-index: 100; }

#controls label {
	display: block;
	cursor: pointer; }

#controls label:hover {
	opacity: 0.8; }

#slide1:checked ~ #controls label:nth-child(2),
#slide2:checked ~ #controls label:nth-child(3),
#slide3:checked ~ #controls label:nth-child(4),
#slide4:checked ~ #controls label:nth-child(5),
#slide5:checked ~ #controls label:nth-child(1) {

	width: 70px;
	height: 70px;
	background: url("../image/Right.png") no-repeat center center;
	float: right;
	display: block; }

#slide1:checked ~ #controls label:nth-child(5),
#slide2:checked ~ #controls label:nth-child(1),
#slide3:checked ~ #controls label:nth-child(2),
#slide4:checked ~ #controls label:nth-child(3),
#slide5:checked ~ #controls label:nth-child(4) {

	width: 70px;
	height: 70px;
	background: url("../image/Left.png") no-repeat center center;
	display: block;
	float: left; }

#slide1:checked ~ #slides .inner {
	margin-left: 0; }

#slide2:checked ~ #slides .inner {
	margin-left: -25%; }

#slide3:checked ~ #slides .inner {
	margin-left: -50%; }

#slide4:checked ~ #slides .inner {
	margin-left: -75%; }

#slide5:checked ~ #slides .inner {
	margin-left: -100%; }

#active {
	text-align: center;
	margin-top: 10px; }

#active label {
	width: 10px;
	height: 10px;
	background: #ccc;
	display: inline-block;
	cursor: pointer;
	border-radius: 2px;
	-webkit-border-radius: 2px;
	-moz-border-radius: 2px; }

#active label:hover {
	background: #999; }

#slide1:checked ~ #active label:nth-child(1),
#slide2:checked ~ #active label:nth-child(2),
#slide3:checked ~ #active label:nth-child(3) {

 background: #333; }

#slides {
	.inner {
		@include transition-ease (0.8s); } }


.blog {
	width: $width;
	// max-width: 95%
	margin: auto;
	font-size: 0 {
  letter-spacing: 0; }
	.blog-block, .date, .web, .submit {
		display: inline-block; }
	.blog-block {
		font-size: 16px;
		border: 1px solid $white;
		box-sizing: border-box;
		position: relative;
		color: $white;
		.date {
			position: absolute;
			top: 30px;
			left: 30px;
			font-family: "FuturaPTH";
			font-size: 24px;
			div:nth-child(2) {
				font-family: "FuturaPTB";
				font-size: 14px; } }
		.web {
			background: rgba(0, 0, 0, 0.1);
			padding: 5px;
			color: $white;
			position: absolute;
			top: 30px;
			right: 30px;
			font-family: "FuturaPT";
			font-size: 14px; }
		.submit {
			position: absolute;
			bottom: 100px;
			left: 30px;
			font-family: "FuturaPTD";
			font-size: 24px; }
		input[type="submit"] {
			position: absolute;
			bottom: 30px;
			left: 30px;
			background: none;
			border: 1px solid $white;
			color: $white;
			padding: 10px 15px;
			font-family: "FuturaPTH";
			font-size: 14px; }
		input[type="submit"]:hover {
			cursor: pointer;
			@include box-shadow(0px 0px 10px 4px rgba(256,112,112,.49)); } } }

.blog-block:nth-child(2) {
	width: 50%;
	height: 479px;
	background: #53b7eb url("../image/Picture-1.png");
	float: left; }
.blog-block:nth-child(3) {
	width: 50%;
	height: 231px;
	background: #f5a93d url("../image/CRM-1.png"); }
.blog-block:nth-child(4) {
	width: 25%;
	height: 248px;
	background: #8367a5 url("../image/Picture-2.png"); }
.blog-block:nth-child(5) {
	width: 25%;
	height: 248px;
	background: #07bd99 url("../image/Picture-3.png"); }

