@import "mixin";

@media screen and (max-width: 720px) {

	body {
		min-width: $w375; }

	.bunner {
		// .ninja
		// 	width: 20px
		// 	height: 20px
		// 	background: url("../image/Menu.png") center center no-repeat
		// 	position: absolute
		// 	left: 15px
		// 	top: 30px

		nav {
			@include transition (0.4s);
			position: absolute;
			top: 0;
			left: -200%;
			background: grey;
			height: 3522px;
			width: 250px;
			@include box-shadow(130px 0px 0px 45px rgba(0,0,0,0.59));
			li {
				display: block;
				text-align: left;
				font-size: 16px;
				margin-left: 15px;
				margin-bottom: 35px;
				top: 0%;
				transform: translateY(0%); }
			li:before {
				content: '\2022';
				color: $white;
				margin-right: 15px; }
			.logo {
				display: block;
				text-align: center;
				margin-top: 20px;
				margin-bottom: 40px; } }
		.ninja:focus ~  nav,
		.ninja:active ~  nav {
			left: 0%;
			z-index: 200;
			overflow: hidden; }
		nav:hover li {
			display: block; }

		.ninja {
			display: block; }

		.st-effect-1.st-menu-open .st-effect-1.st-menu {
			visibility: visible;
			transform: translate3d(340%, 0, 0);
			z-index: 300; } }

	.bunner {
		width: $w375;
		margin: auto;
		background: url('../image/1-Home.png')  center top no-repeat;
		background-size: cover;
		header {
			padding: 20px 0; }
		.logo {
			margin: auto;
			div {
				transform: translateY(-55%);
				font-size: 18px; } }

		.title {
			margin-top: 69px;
			font-size: 22px;
			line-height: 30px;
			div {
				width: 260px;
				margin: 20px auto 0;
				font-size: 16px;
				line-height: 20px; } } }
	.grad {
		margin: 120px 0 0px;
		.new {
			display: inline-block;
			text-align: center;
			margin: 0 0 0 10px;
			.new-block:nth-child(4) {
				float: left;
				margin-left: 20px; }

			.new-block {
				display: inline-block;
				width: 160px;
				height: 60px;
				// vertical-align: top
				margin:  0 0 40px;
				.img {
					width: 70px;
					display: inline-block;
					vertical-align: top;
					text-align: center;
					img {
						margin: auto; } }

				div {
					display: inline-block;
					vertical-align: top;
					width: 84px;
					font-size: 16px;
					line-height: 15px;
					text-align: left;
					div {
						font-size: 14px; } } } } }
	.site-h {
			font-size: 22px;
			margin: 60px 0 30px; }
	.why {
		width: $w375;
		display: block;
		.why-box {
			display: block;
			text-align: center;
			margin-bottom: 20px;
			img {
				width: 47px;
				float: left;
				// display: inline-block
				margin: 0 20px 0 15px; }
			div {
				display: inline-block;
				text-align: left;
				font-size: 16px;

				div {
					display: block;
					margin: 15px 0 0 0;
					font-size: 14px; } } } }

	.portfolio {
		width: $w375;
		display: block;
		margin: auto;
		border: none;
		.sl-capt {
			padding-top: 180px;
			font-size: 16px; }
		.sl2 {
			padding-top: 5px;
			font-size: 14px; }

		nav {
			margin-bottom: 0;
			li {
				a {
					font-size: 12px; } } }

		.inner {
			padding-left: 30px;
			width: 750%; }
		.slide-image {
			height: 240px;
			width: 11%;
			background-position: -30px -25px; }
		#overflow {
			height: 240px; }
		#slide1:checked ~ #controls label:nth-child(2),
		#slide2:checked ~ #controls label:nth-child(3),
		#slide3:checked ~ #controls label:nth-child(4),
		#slide4:checked ~ #controls label:nth-child(5),
		#slide5:checked ~ #controls label:nth-child(1) {
			background: url("../image/Right-2.png") no-repeat center center; }
		#slide1:checked ~ #controls label:nth-child(5),
		#slide2:checked ~ #controls label:nth-child(1),
		#slide3:checked ~ #controls label:nth-child(2),
		#slide4:checked ~ #controls label:nth-child(3),
		#slide5:checked ~ #controls label:nth-child(4) {
			background: url("../image/Left-2.png") no-repeat center center; }
		#slide2:checked ~ #slides .inner {
			margin-left: -82%; }
		#slide3:checked ~ #slides .inner {
			margin-left: -164%; }
		#slide4:checked ~ #slides .inner {
			margin-left: -246%; }
		#slide5:checked ~ #slides .inner {
			margin-left: -328%; } }

	.blog {
		width: $w375;
		.blog-block {
			font-size: 16px;
			.date {
				font-size: 24px;
				top: 15px;
				left: 15px;
				div:nth-child(2) {
					font-size: 14px; } }
			.web {
				font-size: 10px;
				top: 15px;
				right: 15px; }
			.submit {
				font-size: 16px;
				left: 15px; }
			input[type="submit"] {
				bottom: 15px;
				left: 15px;
				padding: 5px 40px; } } }
	.blog-block:nth-child(2) {
		width: 50%;
		height: 374px;
		.submit {
			width: 125px;
			bottom: 230px;
			left: 15px; } }
	.blog-block:nth-child(3) {
		width: 50%;
		height: 186px;
		.submit {
			bottom: 65px; } }
	.blog-block:nth-child(4) {
		width: 50%;
		height: 186px;
		.submit {
			bottom: 50%;
			transform: translateY(50%); } }
	.blog-block:nth-child(5) {
		width: 100%;
		height: 186px;
		.submit {
			bottom: 50%;
			transform: translateY(50%); } }

	.clielts {
		#slider-2 {
			width: $w375; }
		.inner-2 {
			width: 600%; }
		.slide-image-2 {
			margin: 0 10px; }
		.slide-image-2 img {
			height: 25px; }
		#controls-2 {
			width: 110%;
			left: -5%;
			transform: translate(0%, -20%); }
		#slide1-2:checked ~ #slides-2 .inner-2 {
			margin-left: 0; }
		#slide2-2:checked ~ #slides-2 .inner-2 {
			margin-left: -31%; }
		#slide3-2:checked ~ #slides-2 .inner-2 {
			margin-left: -62%; }
		#slide4-2:checked ~ #slides-2 .inner-2 {
			margin-left: -93%; } }

	.contact-us {
		width: $w375;
		height: 330px;
		margin: 384px auto 0;
		// background: url("../image/Location.png") no-repeat
		background-size: cover;
		iframe {
			height: 330px !important; }
		.form {
			width: $w375;
			max-width: 100%; }
		form {
			min-width: $w375;
			height: 334px;
			// margin: auto
			// display: block
			// position: relative
			right: 0;
			top: -334px;
			padding: 0px;

			.form-h {
				font-size: 22px;
				margin: 40px 0 0 15px; }
			input {
				width: 345px;
				margin-top: 30px;
				font-size: 16px;
				margin-left: 15px; }
			input[type="text"]:nth-child(4) {
				width: 345px; }
			input[type="submit"] {
				width: 345px;
				margin-top: 45px; } } }

	footer {
		width: $w375;
		margin: auto;
		padding: 15px 0;
		.foot {
			.logo {
				display: none; }
			nav {
				width: 95%;
				display: block;
				text-align: center;
				margin: auto;
				li {
					margin: 0 10px 25px;
					font-size: 12px; } }
			.ph-e {
				width: 95%;
				display: block;
				margin: auto;
				text-align-last: justify;
				.phone {
					margin: 0;
					font-size: 14px; } }
			.society {
				margin: 0 auto 10px; } }
		.contact {
			font-size: 12px;
			text-align: center;
			margin: 10px auto;
			top: 0; } } }











